import React, {Component} from 'react'
import {connect} from 'react-redux'
import moment from 'moment'
import { Table, Modal, Space, Button, Tooltip, Popconfirm, Tag, Form, Input } from 'antd';
import {actionCreators} from './store'
import {ContentWapper,ContentBox,AddBtnWapper} from './style'
import {
    DeleteOutlined
  } from '@ant-design/icons';
import { put, deleteMethod } from '../../../utils/ajax';

const { Column } = Table;

const layout = {
    labelCol: { span: 6 },
    wrapperCol: { span: 16 },
};
const tailLayout = {
    wrapperCol: { offset:6, span: 16 },
};

class Env extends Component{
    
    
    state = { visible: false ,type:'add',data:{
        testSetName:'',
        testSetDesc:'',
        file:[]
    }};

    showModal = (type,data) => {
        this.setState({
            visible: true,
            type,
            data
        });
    };

    handleFinish = values => {
        console.log(values);

        let url = 'api/v1/auto-lm/lm-env/'+values.env_name+'/'
        put({url,params:{
            env_name:values.env_name,
            display_name:values.display_name
        }}).then(res=>{
            this.reload()
        }).catch()
        this.setState({
            visible: false,
        });
    }

    handleCancel = e => {
        console.log(e);
        this.setState({
        visible: false,
        });
    }

    handleDeleteTableItem = (index,data) =>{
        // delete 操作
        let url = 'api/v1/auto-lm/lm-env/'+data.env_name
        deleteMethod({url,params:{env_name:data.env_name}}).then(res=>{
            this.reload()
        })
    }

    renderEnvStatus(status){
        if(status==="ACTIVE"){
            return(<Tag color="green">{status}</Tag>)
        }

        if(status==="DISABLED"){
            return(<Tag color="default">{status}</Tag>)
        }
        return null
    }

    renderModal(){

        let modelType = "添加"
        if(this.state.type==="edit"){
            modelType = "编辑"
        }

        console.log(this.state.type,this.state.data)

        return(
            <Modal
                title={modelType + "环境"}
                visible={this.state.visible}
                onCancel={this.handleCancel}
                footer={null}
                destroyOnClose
            >
                <Form
                {...layout}
                name="basic"
                initialValues={this.state.data}
                onFinish={this.handleFinish}
                >
                    <Form.Item
                        label="环境名称"
                        name="env_name"
                        rules={[{ required: true, message: '请输入环境名称!' }]}
                    >
                        <Input/>
                    </Form.Item>
                    
                    <Form.Item
                        label="环境描述"
                        name="display_name"
                        rules={[{ required: true, message: '请输入环境描述!' }]}
                    >
                        <Input/>
                    </Form.Item>

                    {/* <Form.Item
                        label="上传测试集"
                        name="file"
                        rules={[{ required: true, message: '请上传测试集!' }]}
                    >
                        <Dragger 
                            name='file'
                            accept=".txt"
                            beforeUpload={(file)  => {
                                const isLt10M = file.size / 1024 / 1024 <= 10;
                                if (!isLt10M) {
                                    message.error("文件大小限制在10M以下！");
                                    return false;
                                }
                                return false;
                            }}
                        >
                            <p className="ant-upload-drag-icon">
                                <InboxOutlined />
                            </p>
                            <p className="ant-upload-text">点击或拖拽到这个区域进行上传</p>
                            <p className="ant-upload-hint">
                                支持txt格式，且文件大小不能超过10MB
                            </p>
                        </Dragger>
                    </Form.Item> */}
                    {/* 创建环境需要一定时间，请耐心等待。 */}
                    <Form.Item {...tailLayout}>
                        <Button type="primary" htmlType="submit">
                            确定
                        </Button>
                    </Form.Item>
                </Form>
            </Modal>
        )
    }

    render(){
          
        function cancel(e) {
            console.log(e);
        }
        const { tableData } = this.props;

        return(
            <ContentWapper>
                <ContentBox>
                    <AddBtnWapper>
                        <Button type="primary" onClick={()=>this.showModal("add",{})}>
                        添加环境
                    </Button>
                    </AddBtnWapper>
                    {this.renderModal()}
                    <Table dataSource={tableData} rowKey={(text, record) => text.env_name}>
                        <Column title="环境名称" width="10" align="center" dataIndex="env_name" key="envName" />
                        <Column title="环境用途描述" width="10" align="center" dataIndex="display_name" key="display_name" />
                        <Column title="环境状态" align="center" dataIndex="env_status" key="env_status" render={(text)=>
                            this.renderEnvStatus(text)
                        }/>
                        <Column title="拥有声学模型" align="center" dataIndex="am_model" key="am_model" render={(text)=>(<>
                            {
                                text?text.map(item=>(<span key={item.file_id}>{item.user_filename}<br/></span>)):null
                            }
                        </>)}/>
                        <Column title="拥有训练集" align="center" dataIndex="train_set" key="train_set"  render={(text)=>(<>
                            {
                                text?text.map(item=>(<span key={item.file_id}>{item.user_filename}<br/></span>)):null
                            }
                        </>)}/>
                        <Column title="拥有测试集" align="center" dataIndex="test_set" key="test_set"  render={(text)=>(<>
                            {
                                text?text.map(item=>(<span key={item.file_id}>{item.user_filename}<br/></span>)):null
                            }
                        </>)}/>
                        <Column title="更新时间"  align="center"dataIndex="updated_at" key="updated_at"   render={(text)=>(
                            <span>{moment(text).format("YYYY-MM-DD HH:mm:ss")}</span>
                        )}/>
                        <Column
                            title="操作"
                            key="action"
                            align="center"
                            render={(text, record, index) => (
                                <Space size="small">
                                    {/* <Tooltip placement="bottomLeft" title='修改'>
                                        <Button onClick={()=>this.showModal("edit",record)} className={"edit-btn"} size="small" icon={<EditOutlined />}></Button>
                                    </Tooltip>   */}
                                    <Popconfirm disabled={record.env_status==="DISABLED"} title="确认删除？" onConfirm={()=>this.handleDeleteTableItem(index,record)} onCancel={cancel} okText="确定" cancelText="取消">
                                        <Tooltip placement="bottomLeft" title='删除'>
                                            <Button disabled={record.env_status==="DISABLED"} type="danger" size="small" icon={<DeleteOutlined />}></Button>
                                        </Tooltip>
                                    </Popconfirm>
                                </Space>
                            )}
                        />
                    </Table>
                </ContentBox>
            </ContentWapper>
        )
    }
    componentDidMount(){
        this.props.handleInitTableData()
    }

    reload = () => {
        this.componentDidMount()
    }
}

const mapStateToProps = (state)=>{
    return {
        tableData: state.getIn(['env','tableData'])
    }
}

const mapDispatch = (dispatch) =>({
    handleInitTableData(){
        dispatch(actionCreators.handleInitTableData())
    },
    // handleDeleteTableItem(index,data){
    //     dispatch(actionCreators.handleDeleteTableItem(index,data))
    //     // message.success('删除成功');
    // }
})

export default connect(mapStateToProps,mapDispatch)(Env);